@import "../../variables.module.scss";

header {
  background-color: $headerColor;
  color: $lightNormalTextColor;
  height: $headerHeight;
}

.headerMiddle {
  width: 75vw;
  min-width: 750px;
}

.loginBlock {
  float: right;
  font-size: 18px;
}

.loginBlock a {
  text-decoration: none;
  color: white;
}
.loginBlock a:active {
  text-decoration: none;
  color: #359eff;
}
.userloginName {
  margin: 0;
}

.navLogin {
  color: white;
}

.musicPlayerBlock {
  position: relative;
  display: inline-block;
  margin-left: 207px;
  font-family: "Comic Sans MS";
  font-size: 15px;
  width: 463px;
}
.bigTitleSize {
  margin-top: 1px;
}
.smallTitleSize {
  font-size: 13px;
  margin-top: 4px;
}

@media (max-width: 767px) {
  .musicPlayerBlock {
    margin: 0;
    display: inline-block;
    width: 356px;
  }
  .smallTitleSize {
    font-size: 10px;
    margin-top: 8px;
  }
  .headerMiddle {
    width: 100vw;
    min-width: 100px;
  }
}

@media (max-width: 450px) {
  .musicPlayerBlock {
    width: 65%;
  }
  .bigTitleSize {
    font-size: 10.5px;
    margin-top: 7px;
  }
  .smallTitleSize {
    margin: 0;
  }
}
@media (max-width: 376px) {
  .musicPlayerBlock {
    width: 65%;
  }
  .bigTitleSize {
    margin: 0;
  }
}
@media (max-width: 280px) {
  .bigTitleSize {
    font-size: 11px;
  }
  .smallTitleSize {
    font-size: 9.5px;
    margin-top: 2px;
  }
}
