@import "./variables.module.scss";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
}

input {
  border-width: 0px;
}

button {
  transition: 0.6s;
}

.app_dark {
  background-color: $darkBackgroundColor;
  min-height: 100vh;
  color: $lightNormalTextColor;
  & button {
    color: black;
    border: none;
    background: $darkBackgroundButtonColor;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.12);
    box-shadow: 0 0 0 rgba(0, 0, 0, 0.045);
    border-radius: 2px;
    margin-left: 0;
  }
  & button:hover {
    background: $darkBackgroundHoverButtonColor;
  }
  & button:active {
    background: $darkBackgroundActiveButtonColor;
  }
  & a {
    color: $darkNormalTextColor;
  }
  & a:active {
    color: $darkActiveLinkColor;
  }
  & a:focus {
    color: $darkActiveLinkColor;
  }
  & input {
    background-color: $darkBackgroundInputColor;
  }
  & input[type="file"]::file-selector-button {
    border: 0px;
    border-radius: 2px;
    background: $darkBackgroundButtonColor;
    transition: 0.6s;
  }
  & input[type="file"]::file-selector-button:hover {
    background: $darkBackgroundHoverButtonColor;
  }
  & input[type="file"]::file-selector-button:active {
    background: $darkBackgroundActiveButtonColor;
  }
}

input[type="file"] {
  background-color: transparent;
}

h1 {
  font-family: "Comic Sans MS";
  color: $darkNormalTextColor;
}
h2 {
  color: $darkNormalTextColor;
}

/*#f44647*/
.app_light {
  background-color: $lightBackgroundColor;
  min-height: 100vh;
  color: $lightNormalTextColor;
  & button {
    color: black;
    border: 1px solid $lightBorderButtonColor;
    background: $lightBackgroundButtonColor;
    border-radius: 2px;
    margin-left: 0;
  }
  & button:hover {
    background: $lightBackgroundHoverButtonColor;
  }
  & button:active {
    background: $lightBackgroundActiveButtonColor;
  }
  & a {
    color: $lightNormalTextColor;
  }
  & a:active {
    color: $lightActiveLinkColor;
  }
  & a:focus {
    color: $lightActiveLinkColor;
  }

  & input[type="file"]::file-selector-button {
    border: 1px solid $lightBorderButtonColor;
    padding: 0.2em 0.4em;
    border-radius: 0.2em;
    background: $lightBackgroundButtonColor;
    transition: 0.6s;
  }
  & input[type="file"]::file-selector-button:hover {
    background: $lightBackgroundHoverButtonColor;
  }
  & input[type="file"]::file-selector-button:active {
    background: $lightBackgroundActiveButtonColor;
  }
}

.app-wrapper {
  display: grid;
  grid-template-areas: "n c";
  grid-template-columns: 200px 1fr;
  width: 100wv;
}

/*@media (max-width: 1200px) {
  .app-wrapper {
    max-width: 970px;
  }
}
@media (max-width: 992px) {
  .app-wrapper {
    max-width: 753px;
  }
}*/
@media (max-width: 767px) {
  .app-wrapper {
    grid-template-columns: 100px 1fr;

    .app-wrapper-content {
      margin-left: 20px;
    }
  }
}

.app_light a:active {
  color: $lightActiveTextColor;
}

.app-wrapper-content {
  max-width: 1240px;
}
